.container {
    padding: 50px;
}

.searchTitle {
    font-family: 'Roboto', sans-serif;
    font-size: 35px;
    color: #F7F9FB;
    margin: 0;
    padding-bottom: 20px;
}

.searchList {
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    color: #F7F9FB;
    text-decoration: none;
    display: flex;
    flex-direction: column;
    max-width: 350px;
    gap: 10px;
}

.searchItem {
    border: 2px solid #2F4454;
    border-radius: 4px;
    width: 700px;
    background-color: #2F4454;
}

.searchItem:hover {
    background: #2E151B;
    border: 2px solid #2E151B;
}

.searchLink {
    display: block;
    padding: 18px;
    text-decoration: none;
    color: #F7F9FB;
    width: 100%;
    font-weight: bold;
}
